import React from "react";
import Layout from "../Components/Layout";
import { graphql, Link } from "gatsby";
import { SEO } from "theme-seo";
import Img from "gatsby-image";

const Features = ({ data, pageContext }) => {
  const markdownNodes = data.allMarkdownRemark.nodes
    .sort((a, b) => new Date(a.parent.birthTime) - new Date(b.parent.birthTime))
    .sort((a, b) => a.frontmatter.order - b.frontmatter.order);
  const templateIcon = data.allFeatureImages.nodes.filter(image => image.name === "video")[0]
  return (
    <Layout faqs={pageContext.faqs}>
      <SEO title={pageContext.title} imageUrl={data.banner && data.banner.publicURL} faqs={pageContext.faqs} />
      {data.banner && data.banner.extension !== "md" &&
        (data.banner.extension === "svg" ? (
          <img
            style={{ width: "100%" }}
            width="1519"
            height="624"
            alt={data.banner.name}
            src={data.banner.publicURL}
          />
        ) : (
          <Img alt={data.banner.name} fluid={data.banner.childImageSharp.fluid} />
        ))}
      <div
        className="flex flex-wrap my-24 justify-center lg:flex-row w-10/12 lg:w-5/6 mx-auto shadow-xl">
        {markdownNodes ? (markdownNodes.map(node => {
        const icon = data.allFeatureImages.nodes.filter(image => node.frontmatter.icon === image.name).length === 1  ?
            data.allFeatureImages.nodes.filter(image => node.frontmatter.icon === image.name)[0] : templateIcon
         return <Link to={`/features/${node.frontmatter.title.replace(/ /g,"-")}`} className="m-1"
          data-aos="fade-up" key={node.id}>
            <div key={node.frontmatter.videoName}>
              <div>
                <div>
                  <div>
                    <div className="shadow-md m-4 p-2 hover:shadow-xl" key={node.fields.slug}>
                      <img className="m-auto w-40 h-40 "
                          src={icon.publicURL}
                          alt={node.frontmatter.title}
                          width="100" height="100"
                      />
                    </div>
                    <h4 className="text-gray-700" style={{fontSize:14, textAlign:"center"}}>{node.frontmatter.title}</h4>
                  </div>
                </div>
              </div>
            </div>
          </Link>
        })) : (
          <div className="m-auto text-purple-600 my-10 font-bold text-4xl">There is no videos to show</div>)}
      </div>
    </Layout>
  );
};

export default Features;

export const query = graphql`
query allVideos {
  allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/feature/"  }})
   {
    nodes {
      html
      fields {
        slug
      }
      frontmatter {
        videoName
        description
        title
        icon
        order
      }
      parent {
          ... on File {
            birthTime(formatString: "MM/DD/YYYY")
          }
        }
    }
  }

  allFeatureImages:allFile(filter: { sourceInstanceName: { eq: "feature" }, extension:{eq: "svg"} }) {
    nodes {
      name
      publicURL
      childImageSharp {
        fixed(width:1200){
         src
         }
       }
    }
  }

  banner: file(name: { eq: "feature" }, extension: { eq: "svg" }) {
    name
    publicURL
    extension
    childImageSharp {
      fluid(maxWidth: 2500) {
        ...GatsbyImageSharpFluid
      }
    }
  }

}`;